import { Col, Row, Container, Image } from 'react-bootstrap';
import React from 'react';
import { NavLink } from 'react-router-dom';
import './css/Catalogue.min.css';
import roller from './img/roller.png';
import electric from './img/electric.png';
import engine from './img/engine.png';
import motorReductor from './img/motorReductor.png';
import pump from './img/pump.png';
import reductor from './img/reductor.png';

const catalogueTopData = [{ id: 2, src: pump, heading: "Насосы", link: "/catalogue/pump" },
{ id: 3, src: engine, heading: "Двигатели", link: "/catalogue/engine" }];

const catalogueBottomData = [{ id: 4, src: reductor, heading: "Редуктора", link: "/catalogue/reductor" },
{ id: 5, src: motorReductor, heading: "Мотор редуктора", link: "/catalogue/motorReductor" },
{ id: 6, src: electric, heading: "Электрические и механические компоненты промышленных производств", link: "/catalogue/electric" }];

function CatalogueItem(props) {
	return (
		<Col lg={3} md={4} sm={6}>
			<NavLink to={props.link}>
				<div className="catalogue__item">
					<Image src={props.imageSrc} rounded />
					<p>{props.heading}</p>
				</div>
			</NavLink>
		</Col>
	);
}

const catalogueTopItems = catalogueTopData.map((item) => <CatalogueItem key={item.id} imageSrc={item.src} heading={item.heading} link={item.link} />);
const catalogueBottomItems = catalogueBottomData.map((item) => <CatalogueItem key={item.id} imageSrc={item.src} heading={item.heading} link={item.link} />);



function Catalogue() {
	return (
		<div className="catalogue">
			<Container>
				<Row className="justify-content-center">
					{/* Костылб (прочитать про тэги в строках) */}
					<Col lg={3} md={4} sm={6}>
						<NavLink to="/catalogue/rollers">
							<div className="catalogue__item">
								<Image src={roller} rounded />
								<p>Пластиковые ролики <br /> <b>GRAND ROLLER</b></p>
							</div>
						</NavLink>
					</Col>
					{catalogueTopItems}
				</Row>
				<Row className="justify-content-center">
					{catalogueBottomItems}
				</Row>
			</Container>
		</div>
	);
}

export default Catalogue;