import CompanyInfo from "../../CompanyInfo/CompanyInfo";
import { Col, Container, Row } from 'react-bootstrap';
import './css/InfoPage.min.css';
import CompanyAdvantage from "../../CompanyAdvantage/CompanyAdvantage";

function CompanyTarget() {
	return (
		<div className="companyTarget">
			<Container>
				<div className="companyTarget__inner">
					<Row>
						<Col xs={12}>
							<h3>Цель компании</h3>
							<p>Целью нашей компании является полное всестороннее обеспечение промышленных объектов всеми необходимыми материальными
							ресурсами для поддержки производственных процессов на запланированном уровне, которые необходимы для рентабельной
							хозяйственной деятельности, как отдельных подразделений, так и всей организации в целом.</p>
						</Col>
					</Row>
				</div>
			</Container>
		</div>
	);
}


function InfoPage() {
	return (
		<div className="infoPage">
			<CompanyInfo />
			<CompanyTarget />
			<CompanyAdvantage />
		</div>
	);
}

export default InfoPage;