import { Col, Container, Row, Image } from 'react-bootstrap';
import './css/CompanyAdvantage.min.css';

import docAdvantageIcon from './img/docAdvantageIcon.png';
import qualityAdvantageIcon from './img/qualityAdvantageIcon.png';
import timeAdvantageIcon from './img/timeAdvantageIcon.png';


function CompanyAdvantage() {
	return (
		<div className="companyAdvantage">
			<Container>
				<Row className="justify-content-center" noGutters>
					<Col xs={12}>
						<h3>Преимущества компании</h3>
					</Col>
					<Col md={4} lg={3} className="text-center">
						<Image src={qualityAdvantageIcon} rounded />
						<p>Высокое качество</p>
					</Col>
					<Col md={4} lg={3} className="text-center">
						<Image src={timeAdvantageIcon} rounded />
						<p>Быстрое исполнение заказов</p>
					</Col>
					<Col md={4} lg={3} className="text-center">
						<Image src={docAdvantageIcon} rounded />
						<p>Оформление всех сопроводительных документов</p>
					</Col>
				</Row>
			</Container>
		</div>
	);
}

export default CompanyAdvantage;