import { Col, Image, Row } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import './css/NewsCard.min.css';

function NewsCard(props) {
	const data = props.newsData;
	return (
		<div className="news__card">
			<Row>
				<Col sm={5}>
					<Image src={data.imgPath} fluid />
				</Col>
				<Col sm={7}>
					<span>{data.date}</span>
					<h5>{data.heading}</h5>
					<p>{data.text}</p>
					<NavLink to={data.link}>Подробнее...</NavLink>
				</Col>
			</Row>
		</div>
	);
}

export default NewsCard;