import { Helmet } from "react-helmet";
import { BrowserRouter, Route } from "react-router-dom";
import Footer from "./components/Footer/Footer";
import CataloguePage from "./components/Pages/CataloguePage/CataloguePage";
import ContactPage from "./components/Pages/ContactPage/ContactPage";
import HomePage from "./components/Pages/HomePage/HomePage";
import InfoPage from "./components/Pages/InfoPage/InfoPage";
import NewsPage from "./components/Pages/NewsPage/NewsPage";
import PartnersPage from "./components/Pages/PartnersPage/PartnersPage";

const { default: Header } = require("./components/Header/Header");

function App() {
	return (
		<BrowserRouter>
			<Helmet>
				<meta charSet="utf-8" />
				<title>Avangard Alliance</title>
				{/* <link rel="canonical" href="http://mysite.com/example" /> */}
			</Helmet>
			<div className="App">
				<Header />
				<Route exact path="/">
					<HomePage />
				</Route>
				<Route path="/catalogue">
					<CataloguePage />
				</Route>
				<Route path="/info">
					<InfoPage />
				</Route>
				<Route path="/contact">
					<ContactPage />
				</Route>
				<Route path="/partners">
					<PartnersPage />
				</Route>
				<Route path="/news">
					<NewsPage />
				</Route>
				<Footer />
			</div>
		</BrowserRouter>
	);
}

export default App;
