import { Col, Row, Container, Image } from 'react-bootstrap';
import { NavLink } from "react-router-dom";
import indexIcon from './img/indexIcon.png';
import companyIcon from './img/companyIcon.png';
import mailIcon from './img/mailIcon.png';
import './css/HeaderTop.min.css';

function HeaderTop() {
	return (
		<div className="headerTop">
			<Container>
				<Row className="justify-content-center">
					<Col className="headerTop__item" xs={4} sm={{ span: 3 }} md={{ span: 2 }}>
						<NavLink to="/">
							<Image src={indexIcon} />
						Главная
						</NavLink>
					</Col>
					<Col className="headerTop__item" xs={6} sm={{ span: 4 }} md={{ span: 3 }}>
						<NavLink to="/info">
							<Image src={companyIcon} />
							О компании
						</NavLink>
					</Col>
					<Col className="headerTop__item" xs={1} sm={{ span: 1, offset: 4 }} md={{ span: 1, offset: 6 }}>
						<NavLink to="/" >
							<Image src={mailIcon} />
						</NavLink>
					</Col>
				</Row>
			</Container>
		</div>
	);
}

export default HeaderTop;
