import { Col, Row, Container, Image } from 'react-bootstrap';
import { NavLink } from "react-router-dom";
import './css/HeaderBottom.min.css';
import logo from './img/logo.png'
function HeaderBottom() {
	return (
		<div className="headerBottom">
			<Container>
				<Row >
					<Col xs={{ span: 6 }}>
						<div className="headerBottom__logo">
							<Image src={logo} />
							<h3>ТОО «Avangard Alliance»</h3>
						</div>
						<p>Email: avangard.alliance@bk.ru</p>
						<p>Telephone: 8 778 932 06 61</p>
					</Col>
					<Col xs={{ span: 6 }}>
						<div className="headerBottom__menu">
							<ul>
								<li><NavLink to="/">Главная</NavLink></li>
								<li><NavLink to="/partners">Партнёрам</NavLink></li>
								<li><NavLink to="/news">Новости</NavLink></li>
								<li><NavLink to="/contact">Контакты</NavLink></li>
							</ul>
						</div>
					</Col>
				</Row>
			</Container>
		</div>
	);
}

export default HeaderBottom;
