import { Col, Container, Image, Row } from 'react-bootstrap';
import { Route } from 'react-router-dom';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import './css/News.min.css';

import news1Img from './img/news1.png';
import news2Img from './img/news2.png';
import bigCardImg from './img/bigCardImg.jpeg';
import NewsCard from './NewsCard/NewsCard';

const newsData = [{ id: 1, imgPath: news1Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/news/1" },
{ id: 2, imgPath: news2Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/news/1" },
{ id: 3, imgPath: news1Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/news/1" },
{ id: 4, imgPath: news2Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/news/1" }];
const news = newsData.map((news) => <NewsCard key={news.id} newsData={news} />);

const BigCardData = { id: 1, imgPath: bigCardImg, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/" };

function NewsSlider() {
	const settings = {
		dots: true,
		infinite: true,
		speed: 500,
		slidesToShow: 2,
		slidesToScroll: 1,
		responsive: [
			{
				breakpoint: 1024,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 2,
					infinite: true,
					dots: true
				}
			},
			{
				breakpoint: 991.98,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1
				}
			}
		]
	};
	return (
		<Slider {...settings}>
			{news}
		</Slider>
	);
}

function NewsBigCard(params) {
	const data = BigCardData;
	return (
		<div className="news__card_big">
			<h3>{data.heading}</h3>
			<Row>
				<Col sm={5}>
					<Image src={data.imgPath} fluid />
				</Col>
				<Col sm={7}>
					<span>{data.date}</span>
					<p>{data.text}</p>
				</Col>
			</Row>
		</div>
	)
}

function News() {
	return (
		<div className="news">
			<Container>
				<div className="news__inner">
					<Route strict path="/news/">
						<NewsBigCard />
					</Route>
					<Route exact path="/">
						<Row>
							<Col xs={12}><h3>Новости</h3></Col>
						</Row>
					</Route>
					<NewsSlider />
				</div>
			</Container>
		</div>
	);
}

export default News;