import { Col, Container, Row, Image, Button, InputGroup, FormControl, Form } from 'react-bootstrap';
import './css/PartnersPage.min.css';
import CompanyAdvantage from "../../CompanyAdvantage/CompanyAdvantage";

import grandRoller from './img/grandRoller.png';
import cnpcAmg from './img/cnpcAmg.png';
import kmkMunai from './img/kmkMunai.png';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

function PartnersSlider() {
	const settings = {
		dots: true,
		infinite: true,
		speed: 500,
		slidesToShow: 3,
		slidesToScroll: 1,
		responsive: [
			{
				breakpoint: 1024,
				settings: {
					slidesToShow: 3,
					slidesToScroll: 2
				}
			},
			{
				breakpoint: 767.98,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 1
				}
			},
			{
				breakpoint: 575.98,
				settings: {
					slidesToShow: 1,
					arrows: false,
					slidesToScroll: 1
				}
			}
		]
	};
	return (
		<Slider {...settings}>
			<div className="partnersSlider__slide">
				<Image src={grandRoller} />
			</div>
			<div className="partnersSlider__slide">
				<Image src={cnpcAmg} />
			</div>
			<div className="partnersSlider__slide">
				<Image src={kmkMunai} />
			</div>
		</Slider>
	);
}

function RequestForm(params) {
	return (
		<div className="requestForm__wrapper">
			<Container>
				<div className="requestForm__inner">
					<Row>
						<Col xs={12}>
							<h3>Оставьте заявку на бесплатную консультацию:</h3>
							<Form className="requestForm">
								<Form.Group controlId="formBasicEmail">
									<Form.Label>Имя </Form.Label>
									<Form.Control type="text" placeholder="Введите имя" />
								</Form.Group>
								<Form.Group controlId="formBasicEmail">
									<Form.Label>Телефон </Form.Label>
									<Form.Control type="tel" placeholder="Введите телефон" />
								</Form.Group>

								<label htmlFor="userEmail">Почта (не обязательно)</label>
								<InputGroup className="mb-3">
									<FormControl
										id="userEmail"
										type="email"
										placeholder="Введиме email"
										aria-label="Recipient's username"
										aria-describedby="basic-addon2"
									/>
									<InputGroup.Append>
										<Button variant="primary">Отправить</Button>
									</InputGroup.Append>
								</InputGroup>
							</Form>
						</Col>
					</Row>
				</div>
			</Container>
		</div>
	)
}

function PartnersPage() {
	return (
		<div className="partnersPage">
			<div className="partnersSlider">
				<Container>
					<Row className="justify-content-center">
						<Col xs={12}>
							<h3>Наши партнеры</h3>
						</Col>
						<Col lg={9}>
							<PartnersSlider />
						</Col>
					</Row>
				</Container>
			</div>
			<CompanyAdvantage />
			<RequestForm />
		</div>
	);
}

export default PartnersPage;