import { Col, Image } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import './css/FooterLogo.min.css';
import logo from '../img/logoFooter.png';
import facebookIcon from '../img/icons/facebookIcon.png';
import instaIcon from '../img/icons/instaIcon.png';
import vkIcon from '../img/icons/vkIcon.png';
import watsappIcon from '../img/icons/watsappIcon.png';
import youtIcon from '../img/icons/youtIcon.png';

function FooterLogo() {
	return (
		<Col sm={6} className="text-center text-sm-left">
			<div className="footer__logo">
				<Image src={logo} />
				<h3>ТОО «Avangard Alliance»</h3>
			</div>
			<p>Email: avangard.alliance@bk.ru</p>
			<p>Telephone: 8 778 932 06 61</p>
			<div className="footer__icons">
				<ul>
					<li><NavLink to="/"><Image src={watsappIcon} /></NavLink></li>
					<li><NavLink to="/"><Image src={facebookIcon} /></NavLink></li>
					<li><NavLink to="/"><Image src={instaIcon} /></NavLink></li>
					<li><NavLink to="/"><Image src={vkIcon} /></NavLink></li>
					<li><NavLink to="/"><Image src={youtIcon} /></NavLink></li>
				</ul>
			</div>
		</Col>
	);
}

export default FooterLogo;