import { Col, Row, Container, Image } from 'react-bootstrap';
import React from 'react';
import './css/CompanyInfo.min.css';
import mapImg from './img/map.png';


function CompanyInfo() {
	return (
		<div className="companyInfo">
			<Container>
				<Row className="justify-content-center">
					<Col xs={12}><h3>О компании</h3></Col>
					<Col lg={6} className="pt-3">
						<p>
							ТОО «Avangard Alliance» занимается материально техническим
							обеспечением предприятий на территории Республики Казахстан.
						</p>
						<p>
							Наша компания - единственный официальный дилер компании
							<span> Grand Roller</span> на территории Республики Казахстан с полной
							поддержкой клиентов и гарантией качества поставляемой продукции.
							В результате постоянного всестороннего мониторинга рынка и
							проведений других маркетинговых исследований, мы стараемся
							предложить максимально выгодные условия поставок и конкурентно
							способные цены.
						</p>
					</Col>
					<Col lg={6} className="text-center">
						<Image src={mapImg} fluid />
					</Col>
				</Row>
			</Container>
		</div>
	);
}

export default CompanyInfo;