import { Col, Container, Row } from 'react-bootstrap';
import NewsCard from '../../News/NewsCard/NewsCard';
import news1Img from './img/news1.png';
import news2Img from './img/news2.png';
import './css/NewsPage.min.css';
import { Route } from 'react-router-dom';
import News from '../../News/News';

const newsData = [{ id: 1, imgPath: news1Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/" },
{ id: 2, imgPath: news2Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/" },
{ id: 3, imgPath: news1Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/" },
{ id: 4, imgPath: news2Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/" },
{ id: 5, imgPath: news1Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/" },
{ id: 6, imgPath: news2Img, date: "15.11.2020", heading: "Заголовок новости", text: "Текст новости", link: "/" }];
const news = newsData.map((news) => <Col md={6} key={news.id} className="mt-3 mb-3"><NewsCard key={news.id} newsData={news} /></Col>);

function NewsPage() {
	return (
		<div className="newsPage">
			<Route exact path="/news">
				<div className="newsPage__wrapper">
					<Container>
						<div className="newsPage__inner">
							<h3>Новости</h3>
							<Container>
								<Row>
									{news}
								</Row>
							</Container>
						</div>
					</Container>
				</div>
			</Route>
			<Route strict path="/news/">
				<News />
			</Route>
		</div>
	);
}

export default NewsPage;