import { Col, Container, Image, Row } from 'react-bootstrap';
import { Route } from 'react-router-dom';
import Catalogue from '../../Catalogue/Catalogue';
import './css/CataloguePage.min.css';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

import catalogueItem1 from './img/catalogueItem1.png';


function CatalogueItem(props) {
	const settings = {
		dots: true,
		infinite: true,
		arrows: false,
		speed: 500,
		slidesToShow: 1,
		slidesToScroll: 1,
	}
	return (
		<div className="catalogueItem">
			<Row className="align-items-center">
				<Col lg={5} xl={4}>
					<Slider {...settings}>
						<div className="catalogueItem__img">
							<Image src={catalogueItem1} fluid rounded />
						</div>
						<div className="catalogueItem__img">
							<Image src={catalogueItem1} fluid rounded />
						</div>
					</Slider>
				</Col>
				<Col lg={7} xl={8} className="mt-5 mt-lg-0">
					<h5>Станции управления и защиты (СУЗ)</h5>
					<p>Станции управления и защиты (СУЗ) - предназначены для автоматического, дистанционного
					или местного регулирования трехфазных двигателей погружных насосов. Основная задача
					– защита насосов от различных аварийных состояний. При работе насосных агрегатов могут
					возникать перегрузки по току, отсутствие воды в скважине, обрыв фазы двигателя. Принцип
					работы СУЗ основан на обработке сигналов датчиков уровня воды, датчиков давления и
					«сухого хода» микроконтроллером, который выдает в автоматическом режиме сигнал на
					запуск или остановку электродвигателя насосного агрегата. Станция выполняет автоматическое
					отключение электродвигателя в любой из ситуаций. Во всех случаях предусмотрена световая
					аварийная сигнализация. СУЗ также обеспечивает восстановление режима работы после
					прекращения аварийного воздействия, а также индикацию потребляемого тока электродвигателя.
					В составе СУЗ предусмотрено реле контроля уровня жидкости сухого хода. Реле регулирует
					процесс подачи жидкости и дренажа в автоматическом режиме.</p>
					<span>Основные функции:</span>
					<ul>
						<li>автоматический контроль уровня воды – отключение насоса при достижении нижнего уровня
						(во время дренажа) и при достижении верхнего уровня (во время наполнения);</li>
						<li>защиту от «сухого хода» – в  случае отсутствия воды на входе насоса, не позволит запустить
						электродвигатель во избежание перегрузки. Данная защита от «сухого хода» с помощью реле
						уровня намного эффективнее косвенной защиты от перегрузки.
						СУЗ может работать в составе с различными типами датчиков (реле) уровня и датчиков (реле)
						давления воды.</li>
					</ul>
					<span>Преимущества:</span>
					<ul>
						<li>простой интерфейс – органы управления и индикации вынесены на переднюю панель шкафа
						управления. Настройка параметров электропривода насоса производится один раз</li>
						<li>надежность и качество за счет использования высококачественных материалов и
						комплектующих</li>
					</ul>
				</Col>
			</Row>
		</div>
	)
}

function CatalogueCategory(props) {
	return (
		<div className="catalogueCategory">
			<Container>
				<h3>Электрические и механические компоненты промышленных производств</h3>
				<CatalogueItem />
			</Container>
		</div>
	)
}

function CataloguePage() {
	return (
		<div className="cataloguePage">
			<Route exact path="/catalogue">
				<Catalogue />
			</Route>
			<Route strict path="/catalogue/">
				<CatalogueCategory />
			</Route>
		</div>
	);
}

export default CataloguePage;